@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
}

body {
  height: 100%; /* Assure que le body occupe toute la hauteur de la page */
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

#pageCenter {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

